import axios from 'axios';
import cookies from 'js-cookie';
import { getLocale } from '~global-js/helpers/locale';
import getEnvironment, { environments } from '~global-js/helpers/environment';

export default function addCouponNewTag(couponNewTag) {
  const locale = getLocale();
  const environment = getEnvironment();
  let url = 'https://gateway.dandythrust.com/graphql';
  if (environment === environments.PROD) {
    url = 'https://gateway.student.com/graphql';
  }

  const param = {
    operationName: 'Header_GetCouponViewed',
    query: `
      query Header_GetCouponViewed {
      viewer {
        ... on Student {
          couponViewed
        }
      }
    }`,
  };

  axios({
    url,
    method: 'post',
    data: param,
    headers: {
      Authorization: cookies.get('STUDENTPORSESSION') ? `Bearer ${cookies.get('STUDENTPORSESSION')}` : '',
      'Accept-Language': locale,
    },
  }).then((response) => {
    if (
      response.data.data &&
      typeof response.data.errors === 'undefined' &&
      response.data.data.viewer &&
      !response.data.data.viewer.couponViewed
    ) {
      couponNewTag.classList.add('header__coupon-new-tag--show');
    }
  }).catch((error) => {
    console.error(error);
  });
}



