import axios from '~global-js/helpers/axios';
import cookies from 'js-cookie';
import { getAuthToken, decodeUserInfo } from '~global/js/helpers/auth';

export default function setWechatQRCode(staticParams, target, isLiveChatCode) {
  const generateQrCodePath = '/wechat-qr-code';
  const allParams = Object.assign({}, staticParams, getUserRelatedParams(isLiveChatCode));

  axios.post(generateQrCodePath, allParams)
  .then((response) => {
    target.setAttribute('src', response.data);
  })
  .catch((error) => {
    throw error;
  });
}

/**
 * Get User-related params for QR code from URL and cookie.
 *
 * @returns {*}
 */
export function getUserRelatedParams(isLiveChatCode) {
  const userRelatedParamsInCookie = ['ga', 'uid', 'sid'];
  const userRelatedParamsInUrlAndCookie = ['utm_medium', 'utm_source', 'utm_campaign', 'utm_content', 'utm_term', 'gclid'];

  let paramsToCheckInUrl = userRelatedParamsInUrlAndCookie;

  // an array of param strings get from url;
  const paramsInUrl = window.location.search.substr(1).split('&');
  // Get params from url
  const userRelatedParams = {};
  paramsInUrl.forEach((paramStr) => {
    const [paramName, paramValue] = paramStr.split('=');
    if (paramValue && paramsToCheckInUrl.includes(paramName)) {
      // If the parameter has been passed from the url, use it.
      userRelatedParams[paramName] = decodeURIComponent(paramValue.replace(/\+/g, ' '));

      if (isLiveChatCode && paramName === 'utm_content') {
        userRelatedParams[paramName] = decodeURIComponent('live_chat'.replace(/\+/g, ' '));
      }

      if (!isLiveChatCode && paramName === 'utm_content') {
        userRelatedParams[paramName] = decodeURIComponent('header'.replace(/\+/g, ' '));
      }

      // Remove it from paramsToCheckInUrl.
      paramsToCheckInUrl = paramsToCheckInUrl.filter(item => item !== paramName);
    }
  });

  // an array that contains those were not found in url and those only in cookie.
  const paramsToCheckInCookie = paramsToCheckInUrl.concat(userRelatedParamsInCookie);
  // Get params from cookie
  paramsToCheckInCookie.forEach((paramName) => {
    const cookieName = (paramName === 'ga') ? '_ga' : paramName;
    const paramValueInCookie = cookies.get(cookieName);
    if (paramValueInCookie) {
      userRelatedParams[paramName] = paramValueInCookie;

      if (isLiveChatCode && paramName === 'utm_content') {
        userRelatedParams[paramName] = decodeURIComponent('live_chat'.replace(/\+/g, ' '));
      }

      if (!isLiveChatCode && paramName === 'utm_content') {
        userRelatedParams[paramName] = decodeURIComponent('header'.replace(/\+/g, ' '));
      }
    }
  });

  const user = decodeUserInfo(getAuthToken());
  if (user) {
    userRelatedParams.user_uuid = user.uuid;
  }
  return userRelatedParams;
}



