import document from 'global/document';
import setWechatQRCode from '~global-js/helpers/set-wechat-qr-code';
import countries from '~global/variables/countries.json';
import customEvents from '~global/variables/custom-events.json';

export default function addQRCode(container) {
  const wechatConversionCode = document.querySelector('.conversation-content__wechat-qr-image');
  const targetQRCode = container.querySelector('.header__wechat-qr-code');
  const liveChatTargetQRCode = document.querySelector('.live-chat__qr-code');

  if (wechatConversionCode) {
    const params = JSON.parse(wechatConversionCode.getAttribute('data-params'));

    if (params) {
      const newParams = Object.assign(params, {
        widget_type: 'Hotline',
      });

      if (window.hurricane && window.hurricane.clientInfo && window.hurricane.clientInfo.country) {
        setQRCodeWithCountryCode(newParams, targetQRCode, false);
        setQRCodeWithCountryCode(newParams, liveChatTargetQRCode, true);
      } else {
        setWechatQRCode(newParams, targetQRCode, false);
        setWechatQRCode(newParams, liveChatTargetQRCode, true);

        // update qrcode after getting client info
        document.addEventListener(customEvents.clientInfo.name, () => {
          setQRCodeWithCountryCode(newParams, targetQRCode, false);
          setQRCodeWithCountryCode(newParams, liveChatTargetQRCode, true);
        });
      }
    }
  } else {
    document.querySelector('.header__wechat').classList.add('hide');
  }
}

function setQRCodeWithCountryCode(params, targetQRCode, isLiveChatCode) {
  if (!window.hurricane || !window.hurricane.clientInfo || !window.hurricane.clientInfo.country) {
    return;
  }
  const countryCode = window.hurricane.clientInfo.country.toLowerCase();
  for (let i = 0, len = countries.list.length; i < len; i++) {
    const country = countries.list[i];
    if (countryCode === country.iso2Code) {
      if (country.phoneCode) {
        const newParams = Object.assign(params, {
          country_code: country.phoneCode.replace('+', ''),
        });
        setWechatQRCode(newParams, targetQRCode, isLiveChatCode);
      }
      break;
    }
  }
}



