import axios from '~global-js/helpers/axios';
import { endPoints } from '~global-js/settings/paths';
import fireCustomEvent from '~global-js/helpers/custom-events';
import { setItem, getItem } from '~global-js/helpers/storage';
import customEvents from '~global/variables/custom-events.json';
import window from 'global/window';

export default function getClientInfo() {
  const storageKey = 'clientInfo';
  const storageData = getItem(storageKey, 'session') || null;

  window.hurricane = window.hurricane || {};

  if (storageData) {
    window.hurricane.clientInfo = storageData;
    fireCustomEvent(customEvents.clientInfo.name);
    return true;
  }

  axios.get(endPoints.clientInfo)
    .then((response) => {
      window.hurricane.clientInfo = response.data;
      setItem(storageKey, response.data, 'session');
      fireCustomEvent(customEvents.clientInfo.name);
    })
    .catch((error) => {
      window.hurricane.clientInfo = window.hurricane.clientInfo || {};
      throw error;
    });

  return true;
}



