import 'element-closest';
import document from 'global/document';
import window from 'global/window';
import { Base64 as base64 } from 'js-base64';
import customEvents from '~global/variables/custom-events.json';
import initGeoPhoneNumber from '~components/geo-phone-number/js/component';
import getClientInfo from '~components/header/js/clientInfo';
import addQRCode from '~components/header/js/addQRCode';
import { getAuthToken, getUserInfo, updateUserInfo, loginUser, logoutUser, decodeUserInfo } from '~global-js/helpers/auth';
import phoneNumberTemplate from '~components/header/templates/phone-number.hbs';
import { getLocale } from '~global-js/helpers/locale';
import cookieSettings from '~global/variables/cookies.json';
import { event, reloadDataAttributeElement } from '~global-js/helpers/tracking';
import cookies from 'js-cookie';
import fireCustomEvent from '~global-js/helpers/custom-events';
import addCouponNewTag from '~global-js/helpers/coupon-new-tag';

export default class HeaderComponent {
  constructor(page) {
    this.header = document.querySelector('header');
    this.cookieBanner = document.querySelector('.cookie-banner');
    this.mobileMenuButton = document.querySelector('.header__nav-item--menu');
    this.searchModalCloseButton = document.querySelector('.header__icon--close-input');
    this.searchButtons = document.querySelectorAll('.js-header__nav-item--search');
    this.subMenuButtons = document.querySelectorAll('.header__nav-has-submenu');
    this.subMenus = document.querySelectorAll('.header__subnav');
    this.accountWrapper = document.getElementById('header-account-wrapper');
    this.searchModal = document.getElementById('header-search-form');
    this.headerPhoneContainer = document.getElementById('header-phone-container');
    this.isOnCN = getLocale() === 'zh-cn';
    this.searchModalContainer = document.querySelector('.header__search-container');
    this.welcomeMessageTemplate = this.accountWrapper.querySelector('.header__nav-link--my-account').innerHTML;
    this.locale = getLocale();
    this.page = page;
    this.scrollY = null;
    this.affiliate = document.querySelector('.header-affiliate__nav-item--search');
    this.couponNewTag = document.getElementById('coupon-new-tag');
  }

  init() {
    this.initMobileMenu();
    this.initSubMenu();
    this.addEventListeners();
    this.initSearchButton();
    this.initCloseSearchModal();
    this.setLocaleCookie();
    initGeoPhoneNumber();
    getClientInfo();

    if (this.isOnCN) {
      addQRCode(document);
    } else {
      this.addCookieBanner();
    }
    this.handleUser();

    if (this.page !== 'searchResultPage' && this.page !== 'propertyPage' && this.page !== 'homePage') {
      this.stickyHeader();
    }

    if (this.page === 'searchResultPage' && this.isOnCN) {
      this.affiliate.classList.add('js-header-affiliate__nav-item--search');
    }

    if (getAuthToken()) {
      addCouponNewTag(this.couponNewTag);
    }
  }

  initSearchButton() {
    for (let i = 0; i < this.searchButtons.length; i++) {
      this.searchButtons[i].addEventListener('click', this.handleSearchButtonClick);
    }
  }

  handleSearchButtonClick = (e) => {
    e.stopPropagation();
    const element = e.target.closest('li');
    const addStateObj = { hash: 'searchOpen' };

    if (element.classList.contains('header__nav-item--active')) {
      this.closeSearchModal(true);
      return;
    }

    HeaderComponent.resetOpenSubMenu();
    this.scrollY = window.scrollY || window.pageYOffset;
    element.classList.add('header__nav-item--active');
    this.searchModal.classList.add('header__search-form--active');
    this.searchModal.style.top = `${this.header.getBoundingClientRect().bottom}px`;
    history.pushState(addStateObj, null, '#searchOpen');

    this.searchModal.querySelector('input').focus();
    document.body.classList.add('no-scroll');
    window.scrollTo(0, 0);

    event({
      c: 'header',
      a: 'click',
      l: 'search > dropDown:open',
    });

    document.addEventListener('keydown', this.keyCodeCloseSearchModal);
    window.addEventListener('popstate', this.initSearchModalHistory);
  };

  initCloseSearchModal() {
    this.searchModal.addEventListener('click', (e) => {
      if (e.target.classList.contains('header__search-form')) {
        this.closeSearchModal(true);
      }
    });
    this.searchModalCloseButton.addEventListener('click', this.closeSearchModal);
  }

  initSearchModalHistory = () => {
    if (history.state === null || Object.keys(history.state).length === 0) {
      this.closeSearchModal(false);
    } else if (history.state.hash === 'searchOpen') {
      this.searchModal.classList.add('header__search-form--active');
      document.querySelector('.header__nav-item--search').classList.add('header__nav-item--active');
      this.scrollY = window.scrollY || window.pageYOffset;
      document.body.classList.add('no-scroll');
      window.scrollTo(0, 0);
    }
  }

  keyCodeCloseSearchModal = (e) => {
    const keyBoardCode = e.keyCode;
    if (keyBoardCode === 27) {
      this.closeSearchModal(true);
    }
  }

  closeSearchModal = (enablePushState) => {
    this.searchModal.classList.remove('header__search-form--active');
    const searchButtonActive = document.querySelector('.header__nav-item--active');
    if (searchButtonActive) {
      searchButtonActive.classList.remove('header__nav-item--active');
      document.body.classList.remove('no-scroll');
      window.scrollTo(0, this.scrollY);
      if (enablePushState) {
        history.pushState({}, null, window.location.href.split('#')[0]);
      }
      document.removeEventListener('keydown', this.keyCodeCloseSearchModal);
    }

    event({
      c: 'header',
      a: 'click',
      l: 'search > dropDown:close',
    });
  }

  initMobileMenu() {
    this.mobileMenuButton.addEventListener('click', this.handleMobileMenu);
  }

  initSubMenu() {
    for (let i = 0; i < this.subMenuButtons.length; i++) {
      this.subMenuButtons[i].addEventListener('click', this.handleSubMenuClick);
    }

    for (let i = 0; i < this.subMenus.length; i++) {
      this.subMenus[i].addEventListener('click', (e) => {
        e.stopPropagation();
      });
    }
  }

  addEventListeners() {
    document.addEventListener(customEvents.loginUser.name, this.login);
    document.addEventListener(customEvents.logoutUser.name, this.logout);
    document.addEventListener(customEvents.userLogin.name, this.showMyAccountMenu);
    document.addEventListener(customEvents.userLogout.name, this.showLoginMenu);
    document.addEventListener(
      customEvents.geoIpPhoneNumberInitialised.name,
      this.updatePhoneNumber
    );
    document.addEventListener(customEvents.updateCouponViewed.name, () => {
      this.removeNewTag();
    });
  }

  login = (e) => {
    if (e.detail && e.detail.token) {
      loginUser(e.detail.token);
    }
  }

  logout = () => {
    logoutUser();
  }

  showMyAccountMenu = () => {
    const authToken = getAuthToken();
    const userInfo = decodeUserInfo(authToken);
    this.accountWrapper.classList.add('header__nav-item--logged-in');

    if (userInfo.roles && userInfo.roles.indexOf('partner') !== -1 && cookies.get(cookieSettings.partnerInfo.name)) {
      const partner = JSON.parse(base64.decode(cookies.get(cookieSettings.partnerInfo.name)));
      this.accountWrapper.querySelector('.header__nav-link--my-account').innerHTML =
        this.welcomeMessageTemplate.replace('user', partner.name);
    } else {
      const user = getUserInfo();
      if (user && (user.first_name || user.last_name)) {
        this.accountWrapper.querySelector('.header__nav-link--my-account').innerHTML =
            this.welcomeMessageTemplate.replace('user', user.first_name || user.last_name);
      }
    }
  }

  showLoginMenu = () => {
    this.accountWrapper.classList.remove('header__nav-item--logged-in', 'header__nav-item--active');
  };

  handleSubMenuClick = (e) => {
    e.stopPropagation();
    const element = e.target.closest('li');

    if (element.classList.contains('header__nav-item--active')) {
      element.classList.remove('header__nav-item--active');
      document.body.removeEventListener('click', HeaderComponent.resetOpenSubMenu);
      return;
    }

    HeaderComponent.resetOpenSubMenu();
    e.target.closest('li').classList.add('header__nav-item--active');
    document.body.addEventListener('click', HeaderComponent.resetOpenSubMenu);

    if (this.searchModal.classList.contains('header__search-form--active')) {
      this.closeSearchModal();
    }
  };

  updatePhoneNumber = (e) => {
    const locale = getLocale();
    const template = phoneNumberTemplate({
      phone: e.detail,
      locale,
      showList: (locale === 'ko-kr') || (e.detail && e.detail.length > 1),
    });

    this.headerPhoneContainer.innerHTML = template;

    if ((locale === 'ko-kr') || (e.detail && e.detail.length > 1)) {
      if (this.isOnCN) {
        addQRCode(this.headerPhoneContainer);
      }

      this.headerPhoneContainer
          .querySelector('.header__nav-has-submenu')
          .addEventListener('click', this.handleSubMenuClick);

      this.headerPhoneContainer.querySelector('.header__subnav').addEventListener('click', (evt) => {
        evt.stopPropagation();
      });
    }
    reloadDataAttributeElement();
  };

  setLocaleCookie() {
    const cookieLocale = cookies.get('locale');

    if (cookieLocale !== this.locale) {
      const parseHost = location.host.split('.');
      const baseDomain = `.${parseHost[1]}.${parseHost[2]}`;

      cookies.set('locale', this.locale, { domain: baseDomain, expires: 3650 });
    }
  }

  addCookieBanner() {
    const whetherCookiesExist = cookies.get('hide_cookie_banner');
    if (typeof whetherCookiesExist === 'undefined' && this.cookieBanner) {
      this.cookieBanner.classList.add('cookie-banner--show');
      this.cookieCloseButton();
    }
  }

  cookieCloseButton() {
    const closeButton = document.querySelector('.cookie-banner__close');
    closeButton.addEventListener('click', () => {
      this.cookieBanner.classList.remove('cookie-banner--show');
      cookies.set('hide_cookie_banner', 'expires', { expires: 365, path: '/' });
    });
  }

  stickyHeader() {
    const nextElement = this.header.nextElementSibling;
    window.addEventListener('scroll', () => {
      if (window.scrollY > 0 || window.pageYOffset > 0) {
        const headerHeight = this.header.offsetHeight;
        this.header.classList.add('sticky-header');

        if (nextElement) {
          nextElement.style.marginTop = `${headerHeight}px`;
        }
      } else {
        this.header.classList.remove('sticky-header');

        if (nextElement) {
          nextElement.style.marginTop = null;
        }
      }
    });
  }

  static resetOpenSubMenu() {
    const activeSubmenus = document.querySelectorAll('.header__nav-item--active');
    const primaryNavi = document.querySelector('.header__nav-list--primary');

    for (let i = 0; i < activeSubmenus.length; i++) {
      activeSubmenus[i].classList.remove('header__nav-item--active');
    }

    primaryNavi.classList.remove('opacity');

    setTimeout(() => {
      primaryNavi.classList.remove('show');
    }, 200);

    document.body.removeEventListener('click', HeaderComponent.resetOpenSubMenu);
  }

  handleMobileMenu = (e) => {
    const element = e.target.closest('li');
    const primaryNavi = document.querySelector('.header__nav-list--primary');

    if (element.classList.contains('header__nav-item--active')) {
      HeaderComponent.resetOpenSubMenu();
      document.body.classList.remove('no-scroll');

      return;
    }

    if (this.cookieBanner) {
      primaryNavi.style.top = `${this.cookieBanner.offsetHeight + this.header.offsetHeight}px`;
    }

    element.classList.add('header__nav-item--active');
    primaryNavi.classList.add('show');

    const showPositionTop = this.header.offsetHeight;
    const showSection = document.querySelector('.show');

    showSection.style.top = `${showPositionTop}px`;

    setTimeout(() => {
      primaryNavi.classList.add('opacity');
      document.body.classList.add('no-scroll');
    }, 10);
  };

  handleUser() {
    const authToken = getAuthToken();
    if (authToken) {
      this.showMyAccountMenu();
      updateUserInfo();
      fireCustomEvent(customEvents.userLogged.name, {
        token: authToken,
      });
    }
    // update menu when received user information
    document.addEventListener(customEvents.receivedUser.name, () => {
      this.showMyAccountMenu();
    });
  }

  removeNewTag() {
    if (this.couponNewTag) {
      this.couponNewTag.classList.remove('header__coupon-new-tag--show');
    }
  }
}



